// 简历信息管理接口
import api from '../utils/api';

// 获取简历列表
export async function getAllList(params) {
    const path = '/env/position/getListSelf';
    const data = await api.get(path, {params});
    return data;
}
// 职位打开
export async function publish(params) {
    const path = '/env/position/publish/'+params;
    const data = await api.post(path);
    return data;
}
// 职位关闭
export async function publishCancel(params) {
    const path = '/env/position/publishCancel/'+params;
    const data = await api.post(path);
    return data;
}


