<template>
  <!-- 职位管理 -->
  <div class="position-wrap" v-loading="loading">
    <div class="position-box">
      <el-button type="primary" class="release" @click="jumpRouter('release')"
        >发布职位</el-button
      >
      <ul class="position-list">
        <li
          v-for="(item, k) in listData.rows"
          :class="{ close: item.status === 3 }"
          :key="k"
        >
          <div class="text">
            <div class="title">
              <h4>{{ item.positionName }}</h4>
              <span>{{ formatWorkAdd(item) }}</span>
            </div>
            <div class="tag">
              <span>{{ item.salaryReq }}</span>
              <span>{{ item.expReq }}年</span>
              <span>{{ item.eduReq }}</span>
            </div>
          </div>
          <div class="opt">
            <span>{{ item.createTime }}发布</span>
            <el-button type="text">编辑</el-button>
            <el-button type="text" @click="closeOpen(item)">{{
              item.status === 3 ? "打开" : "关闭"
            }}</el-button>
          </div>
          <!-- status 为3时状态为关闭 -->
          <el-tag v-if="item.status === 3" size="mini" type="info"
            >职位关闭</el-tag
          >
        </li>
      </ul>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pageInfo.page"
          :page-size="pageInfo.limit"
          layout="prev, pager, next, jumper"
          :total="listData.total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import { getAllList,publish,publishCancel } from "@/api/position";
export default {
  name: "positionManage",
  data() {
    return {
      loading:false,
      listData: [],
      pageInfo: {
        page: 1,
        limit: 10,
      },
    };
  },
  mounted() {
    this.getList(this.pageInfo);
  },
  methods: {
    jumpRouter(path) {
      this.$router.push({ name: path });
    },
    handleSizeChange(val) {},
    handleCurrentChange(val) {},
    // 获取所有职位列表
    getList(param) {
      getAllList(param).then((res) => {
        if (res.code === 200) {
          this.listData = res;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    // 处理工作地址
    formatWorkAdd(item) {
      let _arr = [],
        _str = "-";
      item.workCity && _arr.push(item.workCity);
      item.workAdr && _arr.push(item.workAdr);
      _arr.length && (_str = "[" + _arr.join("·") + "]");
      return _str;
    },
    closeOpen(data){
        let _txt = '',_tit = '';
        (data.status === 2) && (_txt = '职位关闭后，人员将无法查看以及搜索此职位信息',_tit='职位关闭');
        (data.status === 3) && (_txt = '职位通过审核后，将会在职位招聘中进行显示',_tit='职位打开');
        this.$confirm(_txt, _tit, {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.loading = true;
          // 关闭职位
            (data.status === 2) && (this.setPublishCancel(data.id));
            // 打开职位
            (data.status === 3) && (this.setPublish(data.id));
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    setPublish(id){
      let _param = {id:id}
        publish(id).then(res => {
          console.log(res,"000");
          if(res.code === 200){
            this.$message({
          message: '操作成功',
          type: 'success'
        });
          }else {
            this.$message.error(res.msg);
          }
          this.loading = false;
        })
    },
    setPublishCancel(id){
      let _param = {id:id}
        publishCancel(id).then(res => {
          if(res.code === 200){
            this.$message({
          message: '操作成功',
          type: 'success'
        });
          }else {
            this.$message.error(res.msg);
          }
          this.loading = false;
        })
    }
  },
};
</script>
<style lang="less" scoped>
@import "../../assets/variable.less";
.position-wrap {
  background: #f6f6f8;
  padding-bottom: 20px;
  &/deep/.el-button.release {
    width: 150px;
    background-color: #5ed6c8;
    border-color: #5ed6c8;
    margin: 10px 0 0;
  }
}
.position-box {
  width: 1200px;
  margin: 0 auto;
  text-align: left;
}
.position-list {
  overflow: hidden;
  margin: 0 -15px;
  li {
    position: relative;
    .float;
    text-align: left;
    width: 585px;
    margin: 15px;
    background: #fff;
    box-sizing: border-box;
    padding: 25px;
    // color: #abafba;
    color: #32373e;
    &.close {
      color: #abafba;
    }
    .opt,
    .text {
      .inline-block;
    }
    .text {
      width: 280px;
    }
    .opt {
      text-align: right;
      width: 250px;
      & > span {
        margin-right: 10px;
      }
    }
    .title {
      margin-bottom: 5px;
      h4,
      span {
        .inline-block;
      }
      h4 {
        font-size: 16px;
        // color: #333;
      }
      span {
        margin-left: 20px;
      }
    }
    .tag {
      span {
        position: relative;
        margin-right: 11px;
        &::after {
          content: "|";
          display: block;
          position: absolute;
          right: -8px;
          top: 0;
          color: #abafba;
        }
        &:last-child {
          &::after {
            content: "";
          }
        }
        &.money {
          color: #f45c2c;
        }
      }
    }
    /deep/ .el-tag {
      position: absolute;
      top: 10px;
      right: 25px;
    }
  }
}
</style>


